/**
 * A pay check transaction is the completed page after the user clicks on pay.
 * url: /customer/pay-check-transaction/bob/5
 */

import React, { useState, useEffect, createRef } from 'react';

import {
  Container,
  Row,
  Col,
  Button,
  Form,
  Image,
  Accordion,
  Card,
} from 'react-bootstrap';
import { useHistory, useLocation, useParams } from 'react-router';
import CustomerLayout from '../layout/CustomerLayout';
import CheckoutSummaryOrderList from '../components/CheckoutSummaryOrderList';
import styles from '../assets/styles/PayCheck.module.css';
import { useCustContext } from '../CustomerContext';
import { getOrderInfo } from '../api';
import { formatCurrency, renderTax } from '../components/Helper';
import OrderStatus from '../components/OrderStatus';
import RestaurantLogo from '../components/RestaurantLogo';
import QRCode from 'qrcode.react';
import html2canvas from 'html2canvas';
import { useScreenshot, createFileName } from 'use-react-screenshot'
export default function PayCheckInvoice() {
  const history = useHistory();
  const { table_id } = useParams();
  const [amount, setAmount] = useState(0);
  const [orderSummary, setOrderSummary] = useState([]);
  const [total, setTotal] = useState(0);
  const [finalAmount, setFinalAmount] = useState(0);
  const [tipAmount, setTipAmount] = useState(0);
  // const [isSuccessful, setIsSuccessful] = useState(false);
  const path = useLocation();
  const isSuccessful = useLocation().state?.result;
  const { tableInfo, order, taxes, getTaxes, user, createOrder } = useCustContext();
  const [orderFulfillment, setOrderFulfillment] = useState({});
  const [image ,takeScreenShot] = useScreenshot();
  const ref = createRef(null);
  const getOrder = async () => {
    try {
      if (order.id) {
        const res = await getOrderInfo(order.id);
        if(res?.data){
          const order = res.data.data.order;
          setOrderSummary(order.order_items);
          setTotal(order.total);
          setFinalAmount(order.final_total_with_tax_tip)
          if(order?.order_fulfillments.length){
            const order_fulfillments = order.order_fulfillments;
            // get the current order 
            const current = order_fulfillments.find((x) => {
              return x.order_id == order.id;
            })
            setOrderFulfillment(current);
          }
          if(parseFloat(order.tip_amount) > 0){
            setTipAmount(parseFloat(order.tip_amount));
          }
          let num = 0;
          order.order_items.forEach((item) => {
            num = num + item.quantity;
          });
          setAmount(num);
        }

      }
    } catch (err) {
      console.log(err);
    }
  };
  const handleOrderMore = async () =>{
    // for order more, we need to create a new order and assign a new order id to it 
    if(createOrder(table_id, user)){
      history.push(`/customer/table-ordering/${table_id}/menu`);
    }
  }
  const download = (image, { name = "receipt", extension = "jpeg" } = {}) => {
    const a = document.createElement("a");
    a.href = image;
    a.download = createFileName(extension, name);
    a.click();
  };

  const getImage = () => {
    takeScreenShot(ref.current).then(download)
    // var container = document.getElementById('receipt-container');
    // html2canvas(container,{
    //   logging: true,
    //   letterRendering: 1,
    //   useCORS: true
    // }).then(function(canvas){
    //   var link = document.createElement('a');
    //   document.body.appendChild(link);
    //   link.download = 'receipt.jpg';
    //   link.href = canvas.toDataURL();
    //   link.target = '_blank';
    //   link.click();

    // })
  }

  useEffect(() => {
    console.log(path)
    getOrder();
  }, [order]);
  return (
    <CustomerLayout>
      <Container fluid >
        <div id={'receipt-container'}  ref={ref}>
          <div className='mt-3 d-flex justify-content-center'>
            <div className='d-flex flex-column align-items-center'>
              <RestaurantLogo logo={tableInfo.location?.logo?.public_url} />
              {isSuccessful ? (
                <>
                  <div className='d-block text-center mt-3 text-success'>
                    <i
                      className='bi bi-check-circle-fill'
                      style={{ fontSize: 70, color: '#58C2F1' }}
                    ></i>
                  </div>
                  <h5 className='mt-2 mb-0 fw-bold text-secondary' >
                    Payment Successful!
                  </h5>
                  <p className='text-center mt-1'>
                    <div>Order Number:</div>
                    <div className='display-5 d-block fw-bold mb-2' style={{marginLeft: '-5px'}}>
                      #{order.id}
                    </div>
                    <div className='mx-auto w-100'>
                      <QRCode 
                        value={'localhost:3001/customer/pay-check-invoice/84'}
                        size={128}
                        fgColor={'#033F7A'}
                      />
                    </div>
                  </p>
                </>
              ) : (
                <>
                  <div className='d-block text-center mt-3 text-success'>
                    <i
                      class='bi bi-exclamation-circle-fill'
                      style={{ fontSize: 70, color: '#F45757' }}
                    ></i>
                    <h5 className='mt-3 fw-bold' style={{ color: '#33495E' }}>
                      Something went wrong...
                    </h5>
                    <p style={{ color: '#001B36' }}>
                      Please contact our staffs or
                    </p>
                  </div>
                </>
              )}
            </div>
          </div>
          {isSuccessful && <OrderStatus orderFulfillment={orderFulfillment}/>}
          {/* Dropdown orders */}
          <Accordion className='mt-2' defaultActiveKey='0' >
            <Accordion.Item eventKey='0' className={styles.accordion_order}>
              <Accordion.Button className={styles.accordion_order__btn}>
                Your Orders ({amount})
              </Accordion.Button>
              <Accordion.Body className={styles.accordion_order__body}>
                <CheckoutSummaryOrderList orders={orderSummary} readOnly={true} />
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
          {/* price breakdown */}
          <div className='d-flex mb-1 mt-4 justify-content-between align-items-center'>
            <div>Subtotal</div>
            <div className='fw-bold'>
              {formatCurrency(total)}
            </div>
          </div>
          {renderTax(getTaxes(), total)}
          {
            tipAmount > 0 && isSuccessful && 
            <div className='d-flex mb-1 justify-content-between'>
              <div>Tip</div>
              <div className='fw-bold'>{formatCurrency(tipAmount)}</div>
            </div>
          }
          <hr />
          <div className='h4 d-flex mb-3 justify-content-between align-items-center'>
            <div className='fw-normal'>Total</div>
            <div className='fw-bold'>
              {formatCurrency(finalAmount )}
            </div>
          </div>
        </div>
        {isSuccessful ? (
          <div className='d-grid gap-2 mt-4 no-print'  >
            <Button
              variant='outline-primary'
              size="lg"
              className={'py-2 ' + styles.paycheck_border_radius }
              onClick={(e) => window.print()}
            >
              Save Receipt
            </Button>
            <Button
              variant='secondary'
              size='lg'
              className={'py-2 ' + styles.paycheck_border_radius}
              onClick={handleOrderMore}
            >
              Order More
            </Button>
          </div>
        ) : (
          <div className='d-grid gap-2 mt-4'>
            <Button
              variant='secondary'
              size='lg'
              className={'py-3 ' + styles.paycheck_border_radius}
              onClick={() => {
                history.goBack();
              }}
            >
              Try again
            </Button>
          </div>
        )}

        {isSuccessful && (
          <>
            {/* The loyalty point section. */}

            {/* <div className='text-center'>
              <h4 className='text-secondary mt-5 fw-bold text-center'>
                You gained 200 points!
              </h4>
              <p className='text-muted'>
                Sign up to redeem this points and enjoy many perks for loyalty
                program
              </p>
            </div>

            <div className='d-grid gap-2'>
              <Button
                variant='primary'
                size='lg'
                className={'py-3 fw-bold ' + styles.paycheck_border_radius}
                onClick={() => {
                  history.push('/customer/signup');
                }}
              >
                Sign up for loyalty
              </Button>
            </div> */}
            {/* Show the customer what they actually can earn from this   */}

            {/* <div className='mt-4'>
              <h6 className='fw-bold text-secondary text-center'>
                You can redeem this
              </h6>
              <Row className='mt-3'>
                {redeemItems.map((item) => {
                  return (
                    <Col>
                      <Card className='border-0 shadow-sm bg-primary-tint-800'>
                        <Card.Body className='p-2'>
                          <Image fluid src={item.image} className='rounded' />
                          <h6 className='mt-2 mb-0 text-truncate fw-bold'>
                            {item.name}
                          </h6>
                          <p className='mb-0'>{item.point} points</p>
                        </Card.Body>
                      </Card>
                    </Col>
                  );
                })}
              </Row>
            </div> */}
          </>
        )}
      </Container>
    </CustomerLayout>
  );
}
